import { Select, MenuItem, Box } from '@material-ui/core'
import { usePatientSelectorStyles } from 'PatientList/styles'
import { ReactComponent as FilterIcon } from 'assets/icon/filter.svg'
import React, { useState, useCallback, useEffect } from 'react'
import { gql, useQuery } from '@apollo/client'
import dayjs from 'dayjs'
import { Patient, Period, Schedule, Department } from 'common/types'

const GET_SCHEDULES = gql`
  query GetSchedules($from: String, $to: String) {
    schedules(from: $from, to: $to) {
      id
      his_schedule_id
      room_name
      doctor_name
      patients {
        his_patient_id
        name
        gender
      }
    }
  }
`
const SCHEDULES_SUBSCRIPTION = gql`
  subscription onScheduleUpdated($ids: [ID!]!) {
    scheduleUpdated(ids: $ids) {
      id
      patients {
        his_patient_id
        name
        gender
      }
    }
  }
`
// FIXME: fake date for demo
const from = dayjs('2021-03-17T00:00:00Z')

export default function PatientSelector() {
  const { loading, error, data, subscribeToMore } = useQuery<{ schedules: Schedule[] }, Period>(
    GET_SCHEDULES,
    {
      variables: { from: from.toISOString(), to: from.endOf('day').toISOString() },
    }
  )
  const classes = usePatientSelectorStyles()
  const [department, setDepartment] = useState<Department>('morning')

  useEffect(() => {
    if (!data?.schedules) {
      return
    }
    subscribeToMore<{ scheduleUpdated: { id: string; patients: Patient[] } }, { ids: string[] }>({
      document: SCHEDULES_SUBSCRIPTION,
      variables: {
        ids: data.schedules.map((s) => s.his_schedule_id),
      },
      updateQuery: (prev, { subscriptionData }) => {
        if (!subscriptionData.data) return prev
        // Find the updated schedule and replace its patient list
        const updatedScheduleId = subscriptionData.data.scheduleUpdated.id
        const newScheduleData = JSON.parse(JSON.stringify({ ...prev }))
        newScheduleData.schedules.forEach((s: Schedule) => {
          if (s.id === updatedScheduleId) {
            s.patients = subscriptionData.data.scheduleUpdated.patients
          }
        })
        return newScheduleData
      },
    })
  }, [subscribeToMore, data])

  const onDepartmentChange = useCallback((event: React.ChangeEvent<{ value: unknown }>) => {
    setDepartment(event.target.value as Department)
  }, [])

  if (loading || error) {
    return <section className={classes.selector}>{error?.message ?? 'Loading'}</section>
  }

  return (
    <section className={classes.selector}>
      <Box display="flex" justifyContent="space-between" alignItems="center">
        <Select
          classes={{ select: classes.department, icon: classes.icon }}
          value={department}
          onChange={onDepartmentChange}
          disableUnderline
        >
          <MenuItem value="morning">Morning</MenuItem>
          <MenuItem value="afternoon">Afternoon</MenuItem>
          <MenuItem value="evening">Evening</MenuItem>
        </Select>
        <div className={classes.filter}>
          <FilterIcon className="icon" />
          <span>Filter</span>
        </div>
      </Box>
      {data?.schedules.map((s) => {
        return (
          <div key={s.his_schedule_id}>
            {s.doctor_name}, {s.room_name}
            <div>Patients</div>
            {s.patients?.map((p) => {
              return (
                <div key={p.his_patient_id}>
                  {p.his_patient_id}, {p.name}, {p.gender}
                </div>
              )
            })}
          </div>
        )
      })}
    </section>
  )
}
